@import '../../../../node_modules/antd/dist/antd.css';

.menu {
  padding: 0 20px;
}
.ant-menu{
  background-color:unset;
  color:white
}

.ant-menu-horizontal > .ant-menu-item > a{
  color:white;
}

.ant-menu-horizontal > .ant-menu-item > a:hover{
  color:#e5e5e5;
  border-bottom-color: unset;
}
.ant-menu-horizontal .ant-menu-item {
  border-bottom: unset;
}

.ant-menu:hover{
  border-bottom-color: unset;
}

.menu__logo {
  width: 100px;
  float: left;

}

.menu__logo a {
  display: inline-block;
  font-size: 30px;
  padding: 8px 20px;
  color:#E50914;
  font-weight: bold;

}

.menu__container .ant-menu-item {
  padding: 0px 5px;
}

.menu__container .ant-menu-submenu-title {
  padding: 10px 20px;
}

.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
  padding: 10px 15px;
}

.menu__container .ant-menu-horizontal {
  border-bottom: none;
}

.menu__container .menu_left {
  float: left;
  margin-left: 40px;
}

.menu__container .menu_right {
  float: right;
}

.menu__mobile-button {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none !important; /* use of important to overwrite ant-btn */
  background: #E50914;
}

.menu_drawer .ant-drawer-body {
  padding: 0 !important;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
  padding: 14px 24px !important;
}

@media (max-width: 1024px) {
  .menu__logo a {

    padding: 8px 10px;


  }
  .menu__container .menu_left {
    float: left;
    margin-left: 10px;
  }
  .menu__container .ant-menu-item a, .menu__container .ant-menu-submenu-title a{
    padding:10px 5px ;
  }
}



@media (max-width: 767px) {
  .menu__mobile-button {
    display: inline-block !important;
  }

  .menu_left,
  .menu_right {
    display: none;
  }

  .menu__logo a {
    margin-left: -20px;
  }

  .menu__container .ant-menu-item,
  .menu__container .ant-menu-submenu-title {
    padding: 1px 20px;
  }

  .menu__logo a {
    padding: 10px 20px;
  }
}
.ant-menu-horizontal > .ant-menu-item:hover,
.ant-menu-horizontal > .ant-menu-submenu:hover,
.ant-menu-horizontal > .ant-menu-item-active,
.ant-menu-horizontal > .ant-menu-submenu-active,
.ant-menu-horizontal > .ant-menu-item-open,
.ant-menu-horizontal > .ant-menu-submenu-open,
.ant-menu-horizontal > .ant-menu-item-selected,
.ant-menu-horizontal > .ant-menu-submenu-selected {

  border-bottom: none;
}
